@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
@-webkit-keyframes crossout {
  from {
    -webkit-transform: translate(-50%, -50%) scaleX(0);
            transform: translate(-50%, -50%) scaleX(0); }
  to {
    -webkit-transform: translate(-50%, -50%) scaleX(1);
            transform: translate(-50%, -50%) scaleX(1); } }
@keyframes crossout {
  from {
    -webkit-transform: translate(-50%, -50%) scaleX(0);
            transform: translate(-50%, -50%) scaleX(0); }
  to {
    -webkit-transform: translate(-50%, -50%) scaleX(1);
            transform: translate(-50%, -50%) scaleX(1); } }

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lato', Arial, sans-serif;
  color: #3D3D3D;
  background-color: #eee; }

h1 {
  margin-bottom: 0; }

a {
  color: inherit;
  text-decoration: none; }

nav {
  margin: 10px; }

.container {
  text-align: center; }

.App {
  max-width: 600px;
  margin: auto;
  padding: 0 20px; }

header {
  text-align: center; }

form {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  border: 2px solid #3D3D3D;
  border-radius: 20px; }
  form input, form button {
    border: none;
    padding: 5px 20px; }
  form input {
    flex: 0.8 1;
    background-color: white;
    border-radius: 20px 0 0 20px;
    outline: none; }
  form button {
    font-size: 1.0em;
    font-weight: 700;
    flex: 0.2 1;
    color: #3D3D3D;
    background-color: #b7b6ba;
    border-left: 2px solid #3D3D3D;
    border-radius: 0 20px 20px 0;
    cursor: pointer;
    -webkit-transition-duration: .1s;
            transition-duration: .1s;
    outline: none; }
    form button:hover {
      color: #3D3D3D;
      background-color: #FFDE3B; }

.todos {
  color: #3D3D3D; }
  .todos .todo-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px auto;
    padding: 0 20px;
    background-color: #FFCA0A;
    border: 2px solid #3D3D3D;
    border-radius: 20px;
    text-decoration: none;
    cursor: pointer;
    text-transform: uppercase;
    -webkit-transition-duration: .3s;
            transition-duration: .3s; }
    .todos .todo-item:hover {
      background-color: #FFDE3B; }
  .todos .completed {
    color: #9D9CA1;
    border-color: #9D9CA1;
    background-color: #FFDE3B;
    position: relative; }
    .todos .completed:after {
      content: '';
      display: block;
      height: 2px;
      border-radius: 1px;
      width: 98%;
      position: absolute;
      background-color: #9D9CA1;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      -webkit-animation-name: crossout;
              animation-name: crossout;
      -webkit-animation-duration: 0.1s;
              animation-duration: 0.1s;
      -webkit-animation-timing-function: ease-in;
              animation-timing-function: ease-in; }
    .todos .completed:hover {
      background-color: #FFCA0A;
      border-color: #3D3D3D; }
  .todos .fa-trash-alt:hover {
    color: #ff3D3D;
    z-index: 2; }

